import React from 'react'
import {Button} from 'react-bootstrap'
import {toast} from 'react-toastify'

function Display(props){
    const {specUser,alpha3,setModal2,getSiteData,setSpecUser,setUser,setSite,setRobot}=props

    const Delete=async (e)=>{
        const resp=await fetch(`http://localhost:8081/api/deleteUser/${specUser._id}`,{
            method:'DELETE',
            headers:{
                'Content-Type':'application/json',
                'auth-token':localStorage.getItem('auth-token')
            },
        })
        const resp1=await resp.json()
        if(resp1){
            toast.success('User deleted successfully',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            })
            setSpecUser({_id:"",name:"",username:"",Admin:"",Designation:'',Phone:'',Sites:[]})
            setUser(false);setSite(false);setRobot(false)
    
        }
        else {
            toast.error('User not deleted',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            })
        }
    }

    const Update=async ()=>{
        const resp=await fetch(`http://localhost:8081/api/updateUser/${specUser._id}`,{
            method:'POST',
            headers:{
                'Content-Type':'application/json',
                'auth-token':localStorage.getItem('auth-token')
            },
            body:JSON.stringify({name:specUser.name,username:specUser.username,admin:specUser.Admin,designation:specUser.Designation,phone:specUser.Phone})
        })
        const resp1=await resp.json()
        if(resp1.success){
            toast.success('Updated Successfully',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            })
            setUser(false);setRobot(false);setSite(false);
        }
        else {
            toast.error('Not Updated',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            }) 
        }
    }

    return (
        <div className="UserSpecific" style={{padding:"0 5vw",width:"100%"}}>
            <input type="text" onChange={alpha3} name="_id" value={specUser._id} hidden={true} />
            <div style={{paddingTop:"0"}}>Name</div>
            <div style={{paddingTop:"0"}}><input type="text" onChange={alpha3} name="name" value={specUser.name} /></div>
            <div>Username</div>
            <div style={{paddingTop:"0"}}><input type="text" onChange={alpha3} name="username" value={specUser.username} /></div>
            <div>Admin</div>
            <div style={{paddingTop:"0"}}><input type="text" onChange={alpha3} name="Admin" value={specUser.Admin}/></div>
            <div>Phone</div>
            <div style={{paddingTop:"0"}}><input type="text" onChange={alpha3} name="Phone" value={specUser.Phone}/></div>
            <div>Designation</div>
            <div style={{paddingTop:"0"}}><input type="text"  onChange={alpha3} name="Designation" value={specUser.Designation}/></div>
            <div>Sites</div>
            {specUser.Sites.map((val)=>{
                return (<div style={{display:"flex",flexDirection:"row",paddingTop:"0vw",marginBottom:"0.2vw",gap:"0.1vw",border:"1px solid #12D576",borderRadius:"0.3vw"}}>
                    <input type="text" style={{border:"1px solid white"}} value={val} disabled/><svg onClick={()=>{getSiteData(val)}} 
                    height="1.5vw" width="1.5vw" style={{fill:"#8cc63e"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M471.6 21.7c-21.9-21.9-57.3-21.9-79.2 0L362.3 51.7l97.9 97.9 30.1-30.1c21.9-21.9 21.9-57.3 0-79.2L471.6 21.7zm-299.2 220c-6.1 6.1-10.8 13.6-13.5 21.9l-29.6 88.8c-2.9 8.6-.6 18.1 5.8 24.6s15.9 8.7 24.6 5.8l88.8-29.6c8.2-2.7 15.7-7.4 21.9-13.5L437.7 172.3 339.7 74.3 172.4 241.7zM96 64C43 64 0 107 0 160V416c0 53 43 96 96 96H352c53 0 96-43 96-96V320c0-17.7-14.3-32-32-32s-32 14.3-32 32v96c0 17.7-14.3 32-32 32H96c-17.7 0-32-14.3-32-32V160c0-17.7 14.3-32 32-32h96c17.7 0 32-14.3 32-32s-14.3-32-32-32H96z"/></svg>
                    </div>
                )
            })}
            <div   onClick={()=>{setModal2(true)}}style={{cursor:"pointer",padding:'0',fontSize:'1vw',color:"#12D576"}}>+ Add Site</div>
        <div style={{display:"flex",gap:"0.2vw"}}>
          <Button  onClick={(event)=>{Update(event)}} style={{ marginTop: "1vw", backgroundColor: "#12D576", border: "#12D576", padding: "0.5vw 2vw", fontSize: "1vw", fontWeight: "450" }}>Save</Button>
          <Button  onClick={(event)=>{Delete(event)}} style={{cursor:'pointer',marginTop: "1vw",padding: "0.5vw 2vw", fontSize: "1vw", fontWeight: "450" }} variant="danger">Delete</Button>
          </div>

        </div>
    )
}

export default Display
