import React from 'react'
import {Button} from 'react-bootstrap'
import {toast} from 'react-toastify'

function SiteDisplay(props){
    const {alpha1,specSite,setModal3,getRoboData,setUser,setRobot,setSite,setSpecSite}=props
    const Delete1=async (e)=>{
        const resp=await fetch(`http://localhost:8081/api/deleteClient/${specSite.id}`,{
            method:'DELETE',
            headers:{
                'Content-Type':'application/json',
                'auth-token':localStorage.getItem('auth-token')
            }
        })
        const resp1=await resp.json()
        if(resp1.success){
            toast.success('Site deleted successfully',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            })
            setSpecSite({id:"",siteName:"",location:"",gps:"",capacity:"",number:"",robos:[]})
            setUser(false);setSite(false);setRobot(false)
            
        }
        else {
            toast.error('Site not deleted',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            })
        }
    } 
    
    
    const Update1=async ()=>{
        const resp=await fetch(`http://localhost:8081/api/updateClient/${specSite.id}`,{
        method:'POST',
        headers:{
            'Content-Type':'application/json',
            'auth-token':localStorage.getItem('auth-token')
        },
        body:JSON.stringify({name:specSite.siteName,location:specSite.location,gps:specSite.gps,number:specSite.number,capacity:specSite.capacity})
        })
        const resp1=await resp.json()
        if(resp1.success){
            toast.success('Updated Successfully',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            })
            setUser(false);setRobot(false);setSite(false);
        }
        else if(resp1.error){
            toast.error(resp1.error,{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            }) 
        }
        else {
            toast.error('Not Updated',{
                autoClose: 3000,
                closeOnClick: true,
                pauseOnHover: true,
            }) 
        }
    }
    
    return (
        <div className="SiteSpecific" style={{padding:"0 5vw",width:"100%"}}>
        <input type="text" onChange={alpha1} name="id" value={specSite.id} hidden={true} />
        <div style={{paddingTop:"0"}}>Sitename</div>
        <div style={{paddingTop:"0"}}><input type="text"  onChange={alpha1} name="siteName" value={specSite.siteName} /></div>
        <div>Location</div>
        <div style={{paddingTop:"0"}}><input type="text"  onChange={alpha1} name="location" value={specSite.location} /></div>
        <div>Capacity</div>
        <div style={{paddingTop:"0"}}><input type="text" onChange={alpha1} name="capacity" value={specSite.capacity}/></div>
        <div>GPS</div>
        <div style={{paddingTop:"0"}}><input type="text" onChange={alpha1} name="gps" value={specSite.gps} /></div>
        <div>Number</div>
        <div style={{paddingTop:"0"}}><input type="text"  onChange={alpha1} name="number" value={specSite.number}/></div>
        <div>Robos</div>
        {specSite.robos.map((val)=>{
            return (<div style={{display:"flex",flexDirection:"row",paddingTop:"0vw",marginBottom:"0.2vw",gap:"0.1vw",border:"1px solid #12D576",borderRadius:"0.3vw"}}>
                <input type="text" value={val} style={{border:"1px solid white"}} disabled/><svg onClick={()=>{getRoboData(val)}}
                height="1.5vw" width="1.5vw" style={{fill:"#8cc63e"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M471.6 21.7c-21.9-21.9-57.3-21.9-79.2 0L362.3 51.7l97.9 97.9 30.1-30.1c21.9-21.9 21.9-57.3 0-79.2L471.6 21.7zm-299.2 220c-6.1 6.1-10.8 13.6-13.5 21.9l-29.6 88.8c-2.9 8.6-.6 18.1 5.8 24.6s15.9 8.7 24.6 5.8l88.8-29.6c8.2-2.7 15.7-7.4 21.9-13.5L437.7 172.3 339.7 74.3 172.4 241.7zM96 64C43 64 0 107 0 160V416c0 53 43 96 96 96H352c53 0 96-43 96-96V320c0-17.7-14.3-32-32-32s-32 14.3-32 32v96c0 17.7-14.3 32-32 32H96c-17.7 0-32-14.3-32-32V160c0-17.7 14.3-32 32-32h96c17.7 0 32-14.3 32-32s-14.3-32-32-32H96z"/></svg>
                </div>
            )
        })}
        <div onClick={()=>{setModal3(true)}} style={{cursor:"pointer",padding:'0',fontSize:'1vw',color:"#12D576"}}>+ Add Robot</div>
        <div style={{display:"flex",gap:"0.2vw"}}>
          <Button onClick={(event)=>{Update1(event)}} style={{ marginTop: "1vw", backgroundColor: "#12D576", border: "#12D576", padding: "0.5vw 2vw", fontSize: "1vw", fontWeight: "450" }}>Save</Button>
          <Button  onClick={(event)=>{Delete1(event)}} style={{cursor:'pointer',marginTop: "1vw", padding: "0.5vw 2vw", fontSize: "1vw", fontWeight: "450" }} variant='danger'>Delete</Button>
          </div>

    </div>
    )}

export default SiteDisplay
